<template>
    <div v-if="serviceCall" class="container">
        <header class="header">
            <h1>{{ serviceCall.store.store_code }} {{ serviceCall.store.store_name }}</h1>
        </header>
        <div v-if="loading" class="indicator">
            <span>Loading...</span>
        </div>
        <div v-else>
            <div class="content">
                <div class="details">
                    <div class="detail-row">
                        <i class="fa fa-calendar"></i>
                        <span>{{ formatDate(serviceCall.trx_date) }}</span>
                    </div>
                    <div class="detail-row">
                        <i class="fa fa-info-circle"></i>
                        <span>{{ serviceCall.status }}</span>
                    </div>
                    <div class="detail-row">
                        <i class="fa fa-phone"></i>
                        <span>{{ serviceCall.pic }}</span>
                    </div>
                    <div class="detail-row">
                        <i class="fa fa-file-text"></i>
                        <span>{{ serviceCall.subject }}</span>
                    </div>
                    <div class="detail-row">
                        <i class="fa fa-bank"></i>
                        <span>{{ serviceCall.store.address }}</span>
                    </div>
                </div>

                <div v-for="line in lines" :key="line.id" class="line-container">
                    <h2>Unit No: {{ line.unit_no }}</h2>
                    <h3>Complain Details: {{ line.complain_details }}</h3>
                    <input class="input" placeholder="Penyebab" v-model="line.trouble_causer" />
                    <input class="input" placeholder="Solusi" v-model="line.solution" />
                    <div class="photo-container">
                        <button @click="handleAddPhotos(line.id)" style="font-size: x-large;">
                            <i class="fa fa-camera"></i>
                        </button>
                        <div class="photo-preview" v-for="(photo, index) in line.photos" :key="index">
                            <img v-if="photo.document_name"
                                :src="'https://ac.igm.biz.id/public/uploads/media/' + photo.document_name"
                                class="photo" />
                            <img v-else :src="photo.uri" class="photo" />
                        </div>
                    </div>
                </div>
                <div class="button-container">
                    <button class="button cancel-button" @click="handleCancel">Batal</button>
                    <button class="button" @click="handleSave">Simpan</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "@/utils/axios";
import { getLocation } from '@/utils/geo';
import Vue from 'vue'

export default {
    data() {
        return {
            loading: false,
            serviceCall: this.$route.params.serviceCall || null,
            lines: [],
        };
    },
    async created() {
        if (!this.serviceCall) {
            const id = this.$route.params.id;
            await this.fetchServiceCall(id);
        } else {
            this.initializeLines(this.serviceCall.lines);
        }
    },
    methods: {
        async fetchServiceCall(id) {
            this.loading = true;
            try {
                const response = await axios.get(`/service-call/${id}`);
                this.serviceCall = response.data;
                this.initializeLines(this.serviceCall.lines);
            } catch (error) {
                console.error('Error fetching service call:', error);
                Vue.$toast.error('Error', 'Gagal memuat data service call.');
            } finally {
                this.loading = false;
            }
        },
        initializeLines(lines) {
            this.lines = lines.map(line => ({
                ...line,
                // trouble_causer: '',
                // solution: '',
                // photos: [],
            }));
        },
        formatDate(dateString) {
            const options = { year: 'numeric', month: 'long', day: 'numeric' };
            return new Date(dateString).toLocaleDateString(undefined, options);
        },
        handleInputChange(id, field, value) {
            this.lines = this.lines.map(line =>
                line.id === id ? { ...line, [field]: value } : line
            );
        },
        async handleAddPhotos(id) {
            const input = document.createElement("input");
            input.type = "file";
            input.accept = "image/*";
            input.multiple = true;
            input.onchange = (event) => {
                const files = event.target.files;
                const newPhotos = [];
                for (let i = 0; i < files.length; i++) {
                    const file = files[i];
                    const reader = new FileReader();
                    reader.onload = (e) => {
                        newPhotos.push({ uri: e.target.result, file });
                        if (newPhotos.length === files.length) {
                            this.lines = this.lines.map(line =>
                                line.id === id ? { ...line, photos: [...line.photos, ...newPhotos] } : line
                            );
                        }
                    };
                    reader.readAsDataURL(file);
                }
            };
            input.click();
        },
        async handleSave() {
            this.loading = true;
            try {
                const formData = new FormData();
                formData.append('sc_id', this.serviceCall.id);

                const { position } = await getLocation();
                if (position) {
                    formData.append('position', JSON.stringify(position));
                }

                this.lines.forEach((line, index) => {
                    line.photos.forEach((photo) => {
                        if(photo.file){
                            formData.append(`lines[${index}][photos][]`, photo.file);
                        }
                        
                    });
                    formData.append(`lines[${index}][solution]`, line.solution);
                    formData.append(`lines[${index}][trouble_causer]`, line.trouble_causer);
                });

                await axios.post('/service-call/update', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                });
                Vue.$toast.success('Berhasil disimpan')
                this.$router.push({ name: 'ServiceCall', params: { serviceCall: this.serviceCall } });
            } catch (error) {
                console.error('Error saving data:', error);
                Vue.$toast.error('Error', 'Terjadi kesalahan saat menyimpan data');
            } finally {
                this.loading = false;
            }
        },
        handleCancel() {
            this.$router.push({ name: 'ServiceCall', params: { serviceCall: this.serviceCall } });
        },
    },
};
</script>

<style scoped>
.container {
    padding: 16px;
}

.header {
    margin-bottom: 16px;
}

.header h1 {
    font-size: 24px;
    font-weight: bold;
}

.indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.details{
    padding: 10px;
    border-radius: 4px;
    border: #ddd 1px solid;
    margin-bottom: 15px;
    background-color: bisque;
}

.content {
    padding: 16px;
}

.detail-row {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}

.detail-row>* {
    margin-right: 8px;
}

.line-container {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    margin-bottom: 15px;
    background-color: #e9f8cc;
}

.line-container h2 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 8px;
}

.line-container h3 {
    font-size: 18px;
    margin-bottom: 8px;
}

.input {
    width: 100%;
    padding: 8px;
    margin-bottom: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.photo-container {
    display: flex;
    align-items: center;
}

.photo-container button {
    background: none;
    border: none;
    cursor: pointer;
    margin-right: 10px;
}

.photo {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    border-radius: 4px;
    object-fit: cover;
}

.button-container {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
}

.button {
    padding: 10px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    background-color: #007bff;
}

.cancel-button {
    background-color: #dc3545;
}

.cancel-button:hover {
    background-color: #c82333;
}

.button:hover {
    background-color: #0056b3;
}
</style>
