import Vue from 'vue';
import Vuex from 'vuex';
import axios from '@/utils/axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: null,
    isLoading: false,
    statistic: {}
  },
  mutations: {
    setUser(state, userData) {
      state.user = userData;
    },
    setLoading(state, isLoading) {
      state.isLoading = isLoading;
    },
    setStatistic(state, data) {
      state.statistic = data
    }
  },
  actions: {
    async fetchUser({ commit }) {
      try {
        const response = await axios.get('/user');
        commit('setUser', response.data); // Menyimpan data pengguna ke dalam state
      } catch (error) {
        console.error('Failed to fetch user data:', error);
      }
    },
    setLoading({ commit }, isLoading) {
      commit('setLoading', isLoading);
    },
    async setStatistic({ commit }) {
      try {
        const response = await axios.get('/statistic')
        commit('setStatistic', response.data)
      } catch (error) {
        Vue.$toast.error('statistic loading error')
      }
    }
  }
});
